body{
  font-family: "SF Pro SC","SF Pro Display","SF Pro Icons","PingFang SC","Helvetica Neue",Helvetica,Arial,sans-serif,F01e0c,"Microsoft Yahei","Hiragino Sans GB","Heiti SC","WenQuanYi Micro Hei";
}
/*animation*/
/*moveIn moveOut animate*/
.moveIn{-webkit-animation: moveIn 0.8s cubic-bezier(0.165, 0.840, 0.440, 1.000) 0s both;}
.move-in-left{-webkit-animation: move-in-left 0.8s ease 0s both;}
.move-in-right{-webkit-animation: move-in-right 0.8s ease 0s both;}
.movestare{-webkit-animation: movestare 0.8s linear 0s both;}
.moveend{-webkit-animation: moveend 0.8s linear 0s both;}
@-webkit-keyframes moveIn {
	from{-webkit-transform:translate(0,100px);opacity:0;}
	to{-webkit-transform:none;opacity:1;}
}
.fadeIn{-webkit-animation: fadeIn 1s ease .8s both;}
@-webkit-keyframes fadeIn {
	from{opacity:0;}
	to{opacity:1;}
}
@-webkit-keyframes rotate{
	0%{-webkit-transform:rotate(0deg);}
	100%{-webkit-transform:rotate(359deg);}
}
@-webkit-keyframes move-in-left{
	0%{-webkit-transform:translate(500px,0);opacity:0;}
	100%{-webkit-transform:translate(0,0);opacity:1;}
}
@-webkit-keyframes move-in-right{
	0%{-webkit-transform:translate(-500px,0);opacity:0;}
	100%{-webkit-transform:translate(0,0);opacity:1;}
}
@-webkit-keyframes movestare{
	0%{-webkit-transform:translate(92px,0) scale(0.8);opacity:1;}
	100%{-webkit-transform:translate(0,0) scale(1);opacity:1;}
}
@-webkit-keyframes moveend{
	0%{-webkit-transform:translate(0,0) scale(1);opacity:1;}
	100%{-webkit-transform:translate(92px,0) scale(0.8);opacity:1;}
}
